<!-- components/OcrUpload.vue -->
<template>
    <div class="TextOCR">
        <div v-if="isLoading" class="loading-overlay">
            <div class="loader"></div>
        </div>
        <!-- ÂõæÁâá‰∏ä‰º†Âå∫Âüü -->
        <div class="upload-area" @click="triggerUpload" @dragover.prevent="handleDragOver" @drop.prevent="handleDrop">
            <input ref="fileInput" type="file" accept="image/*" @change="handleFileSelect" style="display: none;">
            <div v-if="!previewUrl" class="upload-placeholder">
                <el-icon :size="50">
                    <Upload />
                </el-icon>
                <div class="upload-text">ÁÇπÂáª‰∏ä‰º†ÂõæÁâáÊàñÊãñÊãΩÂà∞Ê≠§Âå∫Âüü</div>
            </div>
            <img v-else :src="previewUrl" class="preview-image" />
        </div>

        <!-- ÊñáÊú¨ÁºñËæëÂå∫Âüü -->
        <div class="text-editor">
            <el-input v-model="ocrText" type="textarea" :rows="15" placeholder="ËØÜÂà´ÁªìÊûúÂ∞ÜÊòæÁ§∫Âú®Ê≠§Â§Ñ" resize="none" />
            <div class="action-buttons">
                <el-button type="primary" @click="handleConfirm" :disabled="!ocrText">
                    Á°ÆËÆ§‰ΩøÁî®
                </el-button>
                <el-button @click="handleCancel">ÂèñÊ∂à</el-button>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, defineEmits } from 'vue'
import { ElMessage } from 'element-plus'

const emit = defineEmits(['confirm', 'cancel'])

const fileInput = ref(null)
const previewUrl = ref('')
const ocrText = ref('')

// Á≠âÂæÖÁä∂ÊÄÅ
const isLoading = ref(false);

// ÂõæÁâá‰∏ä‰º†ÂáΩÊï∞
// ÁÇπÂáª
const triggerUpload = () => {
    fileInput.value?.click()
}
// ÊãñÊãΩÂõæÁâáËá≥Âå∫Âüü
const handleDragOver = (e) => {
    e.dataTransfer.dropEffect = 'copy'
}
const handleDrop = (e) => {
    const file = e.dataTransfer.files[0]
    if (file) handleFileSelect({ target: { files: [file] } })
}

const handleFileSelect = async (e) => {
    const file = e.target.files[0]
    if (!file) return

    // È™åËØÅÊñá‰ª∂
    if (!file.type.startsWith('image/')) {
        ElMessage.error('ËØ∑ÈÄâÊã©ÂõæÁâáÊñá‰ª∂')
        return
    }

    if (file.size > 5 * 1024 * 1024) {
        ElMessage.error('ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá5MB')
        return
    }

    try {
        isLoading.value = true
        previewUrl.value = URL.createObjectURL(file)

        // ÂàõÂª∫FormData
        const formData = new FormData()
        formData.append('file', file)

        // ÂèëÈÄÅËØ∑Ê±Ç
        const response = await fetch('http://localhost:5000/api/TextOCR', {
            method: 'POST',
            body: formData
        })

        const data = await response.json()
        if (data.status === 'success') {
            ocrText.value = data.result
        } else {
            throw new Error(data.message || 'ËØÜÂà´Â§±Ë¥•')
        }
    } catch (error) {
        ElMessage.error(`OCRÈîôËØØ: ${error.message}`)
        handleCancel()
    } finally {
        isLoading.value = false
    }
}

const handleConfirm = () => {
    emit('confirm', ocrText.value)
    reset()
}

const handleCancel = () => {
    emit('cancel')
    reset()
}

const reset = () => {
    previewUrl.value = ''
    ocrText.value = ''
    if (fileInput.value) {
        fileInput.value.value = ''
    }
}
</script>

<style scoped>
.TextOCR {
    display: flex;
    gap: 20px;
    padding: 20px;
    border: 1px solid #ebeef5;
    border-radius: 4px;
}

/* Âä†ËΩΩÂô®Ê†∑Âºè */
.loading-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.8);
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loader {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #8B0012;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.upload-area {
    flex: 1;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    padding: 20px;
    min-height: 300px;
}

.upload-placeholder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: #909399;
}

.preview-image {
    width: 100%;
    height: auto;
    max-height: 500px;
    object-fit: contain;
}

.text-editor {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.action-buttons {
    display: flex;
    gap: 10px;
    justify-content: flex-end;
}
</style>